.DashIcon {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;

    background-color: var(--medlock-blue);
    width: 100%;
    height: 20vh;

    border-radius: 15px;
    padding-top: 10px;
    padding-left: 20px;
    color: white;
    font-family: SourceSansPro;
    font-size: 1.5em;
    font-weight: bold;
    line-height: 1.67;
}


.DashIcon:hover {
    background-color: var(--medlock-dark-blue);
    border: white solid 2px;
}
